@import url(https://fonts.googleapis.com/css?family=K2D);
html, body {
  height:100%!important;
  margin: 0;
  padding: 0;
  background-color: #333333;
}

#root{
  height:100%!important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    height: 100%;
    width: 100%;
}

.center-vertical{
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.home-grid{
    background-image: url(/static/media/back.23459b59.jpg);
    background-size: cover;
    flex: 1 1;
    width: 100%;
    height: 100%
}

.home-welcome-grid{
    position: relative;
    font-family: 'K2D', sans-serif;
    text-align: left;
}


.home-welcome-text-big{
    font-size: 70px;
}

.home-welcome-text-small{
    margin-top: -20px;
    font-size: 20px;
}

.home-welcome-grid p{
    color: white;
}

.home-see-button{
    margin-right: 20px;
    margin-top: 10px;
    border: 2px solid #e7e7e7;
}

.btn-projects{
    margin-right: 20px;
    color: white!important;
    background-color: transparent!important;
    margin-top: 10px;
    padding: 10px;
    border: 2px solid #e7e7e7;
    text-decoration: none!important;
    text-underline: none!important;
}

.home-see-button a{
    color: white!important;
    background-color: transparent!important;
    text-decoration: none!important;
    text-underline: none!important;
}

.home-see-button a:hover{
    background-color: transparent!important;
    text-decoration: none!important;
    text-underline: none!important;
}

.home-see-button:active{
    background-color: transparent!important;
    text-decoration: none!important;
    text-underline: none!important;
}

.about-grid{
    color: white;
    font-family: 'K2D', sans-serif;
}

.about-title-text{
    font-size: 50px;
}

.about-content-text{
    text-align: justify;
    text-justify: inter-word;
    font-size: 16px;
}
.contact-grid span{
    margin-left: 15px;
    font-size: 20px;
    font-style: italic;
    font-family: 'K2D', sans-serif;
}

@media only screen and (max-width: 600px) {
    .contact-grid span{
        margin-left: 15px;
        font-size: 15px;
        font-style: italic;
        font-family: 'K2D', sans-serif;
    }
}

.contact-grid p{
    margin-bottom: 20px;
}
.projects-grid{
    background-color: white;
    padding-top: 100px;
}

@media only screen and (max-width: 600px) {
    .projects-carousel {
        height: 600px;
    }
}


.projects-grid span{
    font-size: 12px;
    font-style: italic;
    font-weight: bold;
}

.carousel-indicators li {
    background-color: #ddd;
}
.carousel-indicators .active {
    background-color: #333333;
}

.projects-carousel{
    min-height: 500px;
}

.project-img-tech img{
    margin-right: 10px;
}
